import React, {useCallback, useContext} from "react";
import styled from "styled-components";
import {ButtonWidget} from "../../../widget/pub/button_widget";
import {TisArbitrationModel} from "../widget/tis_arbitration_model";
import {ADMIN_TYPE} from "../../admin/bottom_list";
import {ModalContext} from "../../../provider/model_provider";
import {useParams} from "react-router-dom";
import {TisResultModel} from "../../admin/widget/result_modal";

export const ChallengeResultWidget: React.FC = () => {
    const {onPresent} = useContext(ModalContext);
    const params = useParams<{ id: string, type: string }>();
    const handleArbitration = useCallback((type: string) => {

        onPresent(<TisResultModel
            claim_id={parseInt(params.id)}
            type={type}
        />)
    }, [params])

    return (
        <WapStyle>
            <div className="title">
                Challenge Result
            </div>

            <div className="buttons">
                <ButtonWidget
                    onClick={() => handleArbitration('true')}
                    bgColor={'#0064FD'}
                    text={'challenge success'}
                    size={{x: 10, y: 40}}
                />
                <ButtonWidget
                    onClick={() => handleArbitration('false')}
                    bgColor={'#0064FD'}
                    text={'challenge fail'}
                    size={{x: 10, y: 40}}
                />
            </div>
        </WapStyle>
    );
}


const WapStyle = styled.div`
  background-color: white;
  margin-top: 20px;

  .buttons {
    display: flex;
    justify-content: space-around;
    padding: 50px 0;
    width: 60%;
    margin: 0 auto;
  }

  .title {
    height: 55px;
    background-color: #EAF2FF;
    line-height: 55px;
    padding-left: 20px;
  }
`
